import React from 'react';
import {Field, Form, Formik} from "formik";
import * as Yup from 'yup'
import {connect} from "react-redux";
import {login} from "../../redux/authReducer";
import {Navigate} from "react-router";


const loginSchema = Yup.object().shape({
  name: Yup.string()
    .required('Введите имя')
    .email('Invalid email'),
  password: Yup.string()
    .required('Введите пароль')
    .max(20, 'Максимальное количество символов 20')
    .min(2, 'Минимальное количество символов 2'),
  // captcha: Yup.string()
  //   .required('Введите капчу'),
})


const LoginForm = (props) => (
  <Formik
    initialValues={{name: '', password: '', captcha: ''}}
    validationSchema={loginSchema}
    onSubmit={(values, actions) => {
      props.login(values.name, values.password, values.isRemembered, values.captcha, actions.setStatus);
      actions.resetForm();
    }}
  >
    {({isSubmitting, errors, touched, status}) => (
      <Form>
        <div className="inputRow">
          <Field type="text" placeholder="email" name="name"/>
          {errors.name && touched.name && <p>{errors.name}</p>}
        </div>
        <div className="inputRow">
          <Field type="password" placeholder="password" name="password"/>
          {errors.password && touched.password && <p>{errors.password}</p>}
        </div>
        <div className="inputCheckbox">
          <Field type="checkbox" id="isRemembered" name="isRemembered"/>
          <label htmlFor="isRemembered">remember me</label>
        </div>
        {props.captchaUrl && <img src={props.captchaUrl} alt="captcha"/>}
        {props.captchaUrl && <Field type="text" placeholder="captcha" name="captcha"/>}
        {/*{errors.captcha && <p>{errors.captcha}</p>}*/}
        <button type="submit" disabled={isSubmitting}>Войти</button>
        <div>
          {status && status.error.map((el, index) => <span key={index}>{el}</span>)}
        </div>
      </Form>
    )}

  </Formik>
);

const Login = ({isAuth, login, captchaUrl}) => {
  if (isAuth) return <Navigate replace to={'/profile'}/>;
  return (
    <>
      <h1>Login</h1>
      <LoginForm login={login} captchaUrl={captchaUrl}/>
    </>
  );
};

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth,
  captchaUrl: state.auth.captchaUrl
})

export default connect(mapStateToProps, {login})(Login);