import React from 'react';
import Profile from "./Profile";
import {connect} from "react-redux";
import {getStatus, getUserProfile, savePhoto, setProfileInfoEditMode, updateProfileInfo, updateStatus} from "../../redux/profileReducer";
import {useLocation, useNavigate, useParams} from "react-router";
import {withAuthRedirect} from "../../hoc/withAuthRedirect";
import {compose} from "redux";

class ProfileContainer extends React.Component {
  refreshProfile() {
    let userId = this.props.router.params.userId;
    if (!userId) userId = this.props.authorisedUserId;
    this.props.getUserProfile(userId);
    this.props.getStatus(userId);
  }

  componentDidMount() {
    this.refreshProfile()
  }

  componentDidUpdate(prevProps) {
    if (this.props.router.params.userId !== prevProps.router.params.userId)
    this.refreshProfile()
  }

  render() {
    return (
      <Profile {...this.props}
               profile={this.props.profile}
               status={this.props.status}
               profileInfoEditMode={this.props.profileInfoEditMode}
               updateStatus={this.props.updateStatus}
               isOwner={!this.props.router.params.userId}
               savePhoto={this.props.savePhoto}
               updateProfileInfo={this.props.updateProfileInfo}
               setProfileInfoEditMode={this.props.setProfileInfoEditMode}
      />
    );
  }
}

let mapStateToProps = (state) => ({
  profile: state.profilePage.profile,
  status: state.profilePage.status,
  authorisedUserId: state.auth.userId,
  isAuth: state.auth.isAuth,
  profileInfoEditMode: state.profilePage.profileInfoEditMode
});

export function withRouter(Component) {
  function ComponentWithRouterProp(props) {
    let location = useLocation();
    let navigate = useNavigate();
    let params = useParams();
    return (
      <Component
        {...props}
        router={{location, navigate, params}}
      />
    );
  }

  return ComponentWithRouterProp;
}

export default compose(connect(mapStateToProps, {getUserProfile, getStatus, updateStatus, savePhoto, updateProfileInfo, setProfileInfoEditMode}),
  withRouter,
  withAuthRedirect
)(ProfileContainer)