const SHOW_GLOBAL_ERROR = 'SHOW_GLOBAL_ERROR';

let initialState = {
  globalError: null,
};

const toggleGlobalError = (globalError) => ({type: SHOW_GLOBAL_ERROR, globalError});

export const showGlobalError = (globalError) => (dispatch) => {
  dispatch(toggleGlobalError(globalError));
  setTimeout( (globalError) => {
    dispatch(toggleGlobalError(null))
  }, 3000 )
};

const commonReducer = (state = initialState, action) => {
  switch (action.type) {
    case SHOW_GLOBAL_ERROR:
      return {
        ...state, globalError: action.globalError
      };
    default:
      return state
  }
};

export default commonReducer