import {profileAPI} from "../api/api";
import {showGlobalError} from "./commonReducer";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';
const SET_PROFILE_INFO_EDIT_MODE = 'SET_PROFILE_INFO_EDIT_MODE';

let initialState = {
  posts: [
    {
      id: 1,
      img: 'http://show-biz.by/gallery/image/gallery_image/18595/1280/_v=64d081547393783',
      text: 'Hello Vova',
      likesCount: 10,
    },
    {
      id: 2,
      img: 'http://show-biz.by/gallery/image/gallery_image/18595/1280/_v=64d081547393783',
      text: 'Hello Sasha',
      likesCount: 0,
    },
    {
      id: 3,
      img: 'http://show-biz.by/gallery/image/gallery_image/18595/1280/_v=64d081547393783',
      text: 'How are yoi?',
      likesCount: 0,
    },
    {
      id: 4,
      img: 'http://show-biz.by/gallery/image/gallery_image/18595/1280/_v=64d081547393783',
      text: 'Fine!',
      likesCount: 13,
    },
  ],
  profile: null,
  profileInfoEditMode: false,
  status: null,
}

export const addPostAC = text => ({type: ADD_POST, text});
const setUserProfile = profile => ({type: SET_USER_PROFILE, profile});
const setStatus = status => ({type: SET_STATUS, status});
export const deletePost = postId => ({type: DELETE_POST, postId});
export const savePhotoSuccess = photos => ({type: SAVE_PHOTO_SUCCESS, photos});
export const setProfileInfoEditMode = bool => ({type: SET_PROFILE_INFO_EDIT_MODE, bool});

export const getUserProfile = (userId) => (dispatch) => {
  profileAPI.getProfile(userId)
    .then(response => {
      dispatch(setUserProfile(response.data));
    })
}


export const getStatus = (userId) => (dispatch) => {
  profileAPI.getStatus(userId)
    .then(response => {
      dispatch(setStatus(response.data));
    })
}

export const updateStatus = (status) => async (dispatch) => {
  try {
    const response = await profileAPI.updateStatus(status);
    if (response.data.resultCode === 0) {
      dispatch(setStatus(status));
    } else {
      const errorsArray = response.data.messages;
      //errorsArray.map( e => console.error(e))
      dispatch(showGlobalError(errorsArray[0]))
    }
  } catch (error) {
    if (error.response.status === 403) {
      dispatch(showGlobalError('Ошибка прав доступа'));
    } else {
      dispatch(showGlobalError(error.message));
    }
  }
}

export const savePhoto = (photo) => async (dispatch) => {
  let response = await profileAPI.savePhoto(photo);
  if (response.data.resultCode === 0) {
    dispatch(savePhotoSuccess(response.data.data.photos));
  }
}

export const updateProfileInfo = (profile) => async (dispatch) => {
  const response = await profileAPI.updateProfileData(profile);
  const userId = profile.userId;

  if (response.data.resultCode === 0) {
    dispatch(getUserProfile(userId));
  } else {
    console.warn(response.data)
  }
}

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      return {
        ...state,
        posts: [...state.posts, {
          id: 5,
          img: 'http://show-biz.by/gallery/image/gallery_image/18595/1280/_v=64d081547393783',
          text: action.text,
          likesCount: 0,
        }],
      };
    }
    case SET_USER_PROFILE: {
      return {...state, profile: action.profile, profileInfoEditMode: false}
    }
    case SET_STATUS: {
      return {...state, status: action.status}
    }
    case DELETE_POST: {
      return {...state, posts: state.posts.filter(p => p.id !== action.postId)}
    }
    case SAVE_PHOTO_SUCCESS:
      return {...state, profile: {...state.profile, photos: action.photos}};
    case SET_PROFILE_INFO_EDIT_MODE: {
      return {...state, profileInfoEditMode: action.bool};
    }
    default:
      return state;
  }
};

export default profileReducer