import {authAPI, securityAPI} from "../api/api";
import {showGlobalError} from "./commonReducer";

const SET_USER_DATA = 'SET_USER_DATA';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const GET_CAPTCHA_URL_SUCCESS = 'GET_CAPTCHA_URL_SUCCESS';

let initialState = {
  userId: null,
  email: null,
  login: null,
  isFetching: false,
  isAuth: false,
  captchaUrl: null,
};

const setAuthUserData = (userId, email, login, isAuth) => ({type: SET_USER_DATA, payload: {userId, email, login, isAuth}});
const toggleIsFetching = (isFetching) => ({type: TOGGLE_IS_FETCHING, isFetching});
const getCaptchaUrlSuccess = (captchaUrl) => ({type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaUrl}});

export const getAuthUserData = () => (dispatch) => {
  dispatch(toggleIsFetching(true))
  return authAPI.me()
    .then(response => {
      if (response.data.resultCode === 0) {
        let {id, email, login} = response.data.data;
        dispatch(setAuthUserData(id, email, login, true))
      }
      dispatch(toggleIsFetching(false))
    })
}


export const login = (email, password, rememberMe, captcha, setServerStatus) => async (dispatch) => {
  const response = await authAPI.login(email, password, rememberMe, captcha);
  if (response.data.resultCode === 0) {
    dispatch(getAuthUserData())
  } else if (response.data.resultCode === 10) {
    dispatch(getCaptchaUrl())
  } else {
    //setServerStatus({error: response.data.messages});
    dispatch(showGlobalError(response.data.messages))
  }
}

export const logout = () => async (dispatch) => {
  const response = await authAPI.logout();
  if (response.data.resultCode === 0) {
    dispatch(setAuthUserData(null, null, null, false))
  }
}


export const getCaptchaUrl = () => async (dispatch) => {
  const response = await securityAPI.getCapcha();
  const captchaUrl = response.data.url;

  dispatch(getCaptchaUrlSuccess(captchaUrl))
};

const authReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_USER_DATA:
    case GET_CAPTCHA_URL_SUCCESS:
      return {
        ...state, ...action.payload,
      }
    case TOGGLE_IS_FETCHING:
      return {
        ...state, isFetching: action.isFetching
      }

    default:
      return state;
  }
};

export default authReducer