import React from 'react';
import {Field, Form, Formik} from "formik";
import styles from "../../Profile/Profile.module.css";
import {MyTextInput} from "../../../utils/formik";

const ProfileDataForm = ({profile, updateProfileInfo}) => {
  return (
    <Formik
      initialValues={{
        lookingForAJob: profile.lookingForAJob || false,
        lookingForAJobDescription: profile.lookingForAJobDescription || '',
        fullName: profile.fullName || 'User',
        aboutMe: profile.aboutMe || 'My Info',
        contacts: {
          github: profile.contacts.github || '',
          vk: profile.contacts.vk || '',
          facebook: profile.contacts.facebook || '',
          instagram: profile.contacts.instagram || '',
          twitter: profile.contacts.twitter || '',
          website: profile.contacts.website || '',
          youtube: profile.contacts.youtube || '',
          mainLink: profile.contacts.mainLink || '',
        }
      }}
      onSubmit={async  (values, {setFieldError}) => {
        //Object.keys(values).map(key => values[key] === undefined ?)
        values.userId = profile.userId;
        updateProfileInfo(values);
        //setFieldError(fieldName, errorMessage)
      }}
      //validationSchema={}
    >
      {({errors, values}) => (
        <Form>
          <MyTextInput
            label="My new name"
            name="fullName"
            type="text"
            placeholder="Change your name"
          />
          <div className={styles.checkboxRow}>
            <label>
              Am I looking for a job:
              <Field
                name='lookingForAJob'
                type="checkbox"
              />
            </label>
            {values.lookingForAJob ? <label className={styles.lookingForAJobDescription}>
              CV description:
              <Field
                name='lookingForAJobDescription'
                as="textarea"
              />
            </label> : undefined}
          </div>
          <MyTextInput
            label="about Me"
            name="aboutMe"
            type="text"
            placeholder="about Me"
          />
          <div>
            <h3>Contacts:</h3>
            <MyTextInput
              label="Github"
              name="contacts.github"
              type="text"
              placeholder="Github"
            />
            <MyTextInput
              label="VK"
              name="contacts.vk"
              type="text"
              placeholder="Вконтакте"
            />
            <MyTextInput
              label="facebook"
              name="contacts.facebook"
              type="text"
              placeholder="Facebook"
            />
            <MyTextInput
              label="Instagram"
              name="contacts.instagram"
              type="text"
              placeholder="Instagram"
            />
            <MyTextInput
              label="Twitter"
              name="contacts.twitter"
              type="text"
              placeholder="Twitter"
            />
            <MyTextInput
              label="My site"
              name="contacts.website"
              type="text"
              placeholder="Site url"
            />
            <MyTextInput
              label="Youtube"
              name="contacts.youtube"
              type="text"
              placeholder="Youtube"
            />
            <MyTextInput
              label="mainLink"
              name="contacts.mainLink"
              type="text"
              placeholder="mainLink"
            />
          </div>
          <button className={styles.submit} type={'submit'}>Save</button>
        </Form>
      )}
    </Formik>
  );
};

export default ProfileDataForm;