import styles from './MyPosts.module.css'
import Post from "./Post/Post";
import * as React from "react";
import {Field, Form, Formik} from "formik";
import * as Yup from 'yup'


const MyPostsSchema = Yup.object().shape({
  newPostText: Yup.string()
    .max(20, 'Too Long!')
    .required('Required'),
});

const MyPosts = React.memo((props) => {
  let postComponent =
    props.posts.map(post => <Post key={post.id} img={post.img} likesCount={post.likesCount} text={post.text}/>);

  let onAddPost = (text) => {
    props.addPost(text);
  };

  return (
    <div>
      <h3>My posts</h3>
      <div className={styles.textarea__wrapper}>
        <MyPostTextarea onAddPost={onAddPost}/>
      </div>
      <div className={styles.wrap}>
        {postComponent}
      </div>
    </div>
  )
});

const MyPostTextarea = (props) => {
  const addMessage = (text) => {
    props.onAddPost(text)
  }
  return (
    <Formik
      initialValues={{newPostText: ""}}
      onSubmit={(values, {resetForm}) => {
        addMessage(values.newPostText);
        resetForm({values: ''})
      }}
      validationSchema={MyPostsSchema}
    >
      {
        ({errors}) => (
          <Form>
            <Field
              name={'newPostText'}
              as={'textarea'}
              placeholder={'Enter your post'}
            />
            {errors.newPostText ? (
              <div className={styles.error}>{errors.newPostText}</div>
            ) : null}
            <button type={'submit'}>enter</button>
          </Form>
        )
      }
    </Formik>
  )
}

export default MyPosts