import React, {useState} from 'react';
import styles from './ProfileInfo.module.css'
import Preloader from "../../common/preloader/Preloader";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
import noUser from "../../../assets/img/no-user.png";
import ProfileDataForm from "./ProfileDataForm";

const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto, updateProfileInfo, profileInfoEditMode, setProfileInfoEditMode}) => {
  //let [editMode, setEditMode] = useState(false);
  if (!profile) {
    return <Preloader/>
  }


  const onMainPhotoSelected = (e) => {
    if (e.target.files.length) savePhoto(e.target.files[0])
  }
  return (
    <div className={styles.info}>
      <img src={profile.photos.large || noUser}
           alt={profile.fullName} className={styles.mainPhoto}
      />
      {isOwner && <input type="file" onChange={onMainPhotoSelected}/>}
      <div className={styles.name}>{profile.fullName}</div>
      <div className={styles.about}>{profile.aboutMe}</div>
      <ProfileStatusWithHooks status={status} updateStatus={updateStatus} isOwner={isOwner}/>

      {profileInfoEditMode
        ? <ProfileDataForm profile={profile} updateProfileInfo={updateProfileInfo}/>
        : <ProfileData profile={profile} isOwner={isOwner} goToEditMode={() => setProfileInfoEditMode(true)}/>
      }
    </div>
  );
};


const Contact = ({name, value}) => {
  return (
    <div><b>{name}</b>: <span>{value}</span></div>
  )
};

const ProfileData = ({profile, isOwner, goToEditMode}) => {
  return (
    <>
      <div>
        {
          profile.lookingForAJob && profile.lookingForAJobDescription
            ? <span><b>CV description: </b>{profile.lookingForAJobDescription}</span>
            : undefined
        }
      </div>
      <div className={styles.contacts}>
        <h3>Contacts:</h3>
        {Object.keys(profile.contacts).map(key => <Contact key={key} name={key} value={profile.contacts[key]}/>)}
      </div>
      {isOwner && <button onClick={goToEditMode}>Edit</button>}
    </>
  )
}


export default ProfileInfo;